import type { AutosuggestSource } from "@rp/react/tanstack-api/content"
import { SourceSelectorSelectedPackageInfo } from "../sourceSelector.types"

function getPackageAndSourceIntersectionFunc(
  autosuggestSource: AutosuggestSource,
  selectedAllPackages: Array<SourceSelectorSelectedPackageInfo>,
): Array<string> | null {
  const matchedPackages = selectedAllPackages.filter(contentPackage =>
    autosuggestSource.rpPackageId.includes(contentPackage.id),
  )
  const matchedPackageNames = matchedPackages.map(contentPackage => contentPackage.name)

  return matchedPackageNames.length > 0 ? matchedPackageNames : null
}

export { getPackageAndSourceIntersectionFunc }
